.ias-bg {
  background: #15142b;
  color: #fff;
  padding-top: 60px;
  padding-bottom: 60px;
}
.ias-label {
  color: #fff;
  font-family: "Urbanist";
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
}


.seeall {
  border-radius: 4px;
  border: 1px solid #fff;
  color: #fff;
  background: transparent;
  padding: 8px 20px;
  display: inline-flex;
  padding: 12px 24px;
  font-family: Urbanist;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  letter-spacing: 0.15px;
}
.add-post-btn {
  border-radius: 4px;
  border: 1px solid #fff;
  background: #fff;
  border-radius: 4px;
  padding: 8px 20px;
  display: inline-flex;
  padding: 12px 24px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  text-align: center;
  font-family: Urbanist;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  letter-spacing: 0.15px;
}
.card-Featured {
  position: relative;
  border-radius: 16px;
  border: 1px solid rgba(101, 130, 255, 0.18);
  background: linear-gradient(0deg, #150f22 0%, #1b112b 100%);
  color: #fff;
  overflow: hidden;
  padding: 15px 15px 30px 15px;
  h5 {
    color: #fff;
    font-family: Urbanist;
    font-size: 17.682px;
    font-style: normal;
    font-weight: 500;
    line-height: 19.647px; /* 111.111% */
    display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  }
  p {
    color: rgba(255, 255, 255, 0.52);
    font-family: Urbanist;
    font-size: 15.718px;
    font-style: normal;
    font-weight: 500;
    line-height: 19.647px; /* 125% */
  }
}
.featurecardbg {
  position: absolute;
  bottom: 0px;
  left: 0px;
  z-index: 0;
}
.featureprofilebg {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 0;
}
.recentbg {
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 0;
}
.z-1 {
  position: relative;
  z-index: 1;
}
.imgcontainer {
  width: 100%;
  height: 320px;
  img {
    object-fit: cover;
    border-radius: 16px;
    height: 100%;
    width: auto;
  }
}
.user {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #fff;
  font-family: Urbanist;
  font-size: 13.753px;
  font-style: normal;
  font-weight: 500;
  line-height: 19.647px; /* 142.857% */
  span {
    color: rgba(255, 255, 255, 0.37);
    font-family: Urbanist;
    font-size: 13.753px;
    font-style: normal;
    font-weight: 500;
    line-height: 19.647px; /* 142.857% */
  }
}
.profile-imgg {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid #fff;
}
.profile-avatar {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid #fff;
  background: rgba(255, 255, 255, 0.28);
}
.Featured-head {
  color: #fff;
  font-family: Urbanist;
  font-size: 15.718px;
  font-style: normal;
  font-weight: 500;
  line-height: 19.647px; /* 125% */
}
.badge-trending {
  border-radius: 18px;
  border: 1px solid rgba(101, 130, 255, 0.4);
  background: rgba(255, 255, 255, 0.22);
  padding: 4px 12px;
  color: #fff;
  font-family: "Urbanist";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
}
.Featuredtitle {
  color: #fff;
  font-family: Urbanist;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px; /* 90.909% */
}
.profile-pic {
  height: 110px;
  width: 110px;
  object-position: top;
  border-radius: 50%;
  object-fit: cover;
}
.profile-details {
  text-align: center;
  h6 {
    color: #c7c7c7;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    margin-bottom: 0;
    font-weight: 500;
    line-height: 20px; /* 111.111% */
  }
  p {
    color: rgba(255, 255, 255, 0.63);
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px; /* 142.857% */
  }
}
.quote {
  padding: 14px;
  font-family: Urbanist;
  font-size: 20px;
  font-style: italic;
  font-weight: 400;
  line-height: 34.383px; /* 171.917% */
  opacity: 0.65;
  background: linear-gradient(180deg, #fff 0%, #1b112b 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.girlimg {
  width: 100%;
  height: 100%;
  max-height: 260px;
  object-fit: cover;
  object-position: center;
  border-radius: 16px;
}
